
  import { Component, Vue } from 'vue-property-decorator'
  import { getStorehouseList, addStorehouse, putStorehouse, delStorehouseList, getCompanyList} from '@/api/admin'
  import { IArticleData } from '@/api/types'
  import { Form } from 'element-ui'

  @Component({
    name: 'warehouse'
  })

  export default class extends Vue {
    private list: IArticleData[] = []
    private listLoading = true
    private dialogFormVisible = false
    private title = '新增仓库'
    private total = 0
    private sub = ''
    private companyList = []
    private listQuery = {
      page: 1,
      pagesize: 10,
      company: ''
    }
    private initForm = {
      housecode: '',
      companyid: '',
      companyname: '',
      housetype: '',
      houseaddress: '',
      housename: '',
      housearea: '',
      principalname: '',
      principaltel: '',
      areasize: '',
      housestatus: ''
    }
    private form = {
      ...this.initForm
    }
    private formLabelWidth = '120px'
    private rules = {
      housecode: [
        { required: true, message: '请输入仓库编号', trigger: 'blur' }
      ],
      companyid: [
        { required: true, message: '请选择所属企业', trigger: 'blur' }
      ],
      housename: [
        { required: true, message: '请输入库房名称', trigger: 'blur' }
      ],
      housetype: [
        { required: true, message: '请选择库房类型', trigger: 'change' }
      ],
      housestatus: [
        { required: true, message: '请选择库房类型', trigger: 'change' }
      ]
    }
    created() {
      this.getList()
      this.getCompanyList()
    }
    // 获取企业列表
    private async getCompanyList() {
      const { data } = await getCompanyList(this.listQuery)
      this.companyList = data.items
    }
    //选择公司时，赋值隐藏域
    private companyChange(data: any) {
      //console.log(item);
      this.form.companyname = data.company;
    }

    // 列表数据获取
    private async getList() {
      this.listLoading = true
      const { data } = await getStorehouseList(this.listQuery)
      this.list = data.items
      this.total = data.total
      this.listLoading = false
    }
    // 新增 修改
    private async changeHandel(row:any) {
      this.dialogFormVisible = true
      if (row) {
        this.title = '修改仓库'
        this.sub = 'put'
        this.form = row
      } else {
        this.title = '新增仓库'
        this.sub = 'add'
        this.form = this.initForm
      }
    }
    // 提交
    private async submitHandle() {
      (this.$refs.form as Form).validate(async(valid) => {
        if (valid) {
          if (this.sub === 'add') {
            const res = await addStorehouse(this.form)
            if (res.status === 200) {
              this.$message({
                type: 'success',
                message: '添加成功!'
              })
              this.getList()
            }
          } else {
            const res = await putStorehouse(this.form)
            if (res.status === 200) {
              this.$message({
                type: 'success',
                message: '修改成功!'
              })
            }
            this.getList()
          }
          this.dialogFormVisible = false
        }
      })
    }
    // 删除
    private async delHandel(id:any) {
      this.$confirm('确定要删除该仓库？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const res = delStorehouseList({ id })
        if (res) {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.getList()
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
    // 分页切换
    private async handleCurrentChange(page:any) {
      this.listQuery.page = page
      this.getList()
    }
  }
